/*-------- 1.4 menu --------*/
.main-menu {
  position: relative;
  z-index: 9;
  width: auto; }

.main-menu a {
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s; }

.main-menu ul,
.main-menu ul li,
.main-menu ul li a {
  position: relative;
  margin-bottom: 0;
  margin: 0;
  padding: 0; }

/* Submenu styles */
.main-menu ul li a {
  display: block;
  line-height: 20px;
  padding: 10px; }

/*First level styles */
.main-menu > ul > li > a {
  color: #fff;
  padding: 0 8px 15px 8px;
  font-size: 13px; }

.sticky .main-menu > ul > li > a {
